export default {
	computed: {

		awf() {
			return (str) => {

				const randomWord = (words) => {
					let index = Math.floor(Math.random() * words.length);
					return words[index]
				};

				abusiveWords.forEach(({exp, flag, wordsToReplace}) => {
					str = str.replace(new RegExp(exp, flag), randomWord(wordsToReplace))
				});

				return str;

			}
		}

	}

}


